/* global Parse */
import React, { useState, useCallback } from "react";
import {
  Box,
  Button,
  TextInput,
  Heading,
  Image,
  CheckBox
} from "grommet";
import {useDropzone} from 'react-dropzone'
import Parse from '../../library/parse'
const Component = ({ formModal, onModal, mode = 'create', onDelete, onUpdate }) => {

  const [modal, setModal] = useState(formModal)
  const [checked, setChecked] = React.useState(true);
  const [, setProductImage] = useState(null)

  const textInputProps = (key, modalObject) => ({
    value: modalObject[key],
    onChange: event => setModal({
        ...modalObject,
        [key]: event.target.value
    })
  })

  const onDrop = useCallback(acceptedFiles => {
    const uploadFile = (file) => {
      var parseFile = new Parse.File(file.name, file);
      parseFile.save().then(function (parseFile) {
        setProductImage(parseFile.url())
        setModal({
          ...modal,
          photo: parseFile,
          available: !!checked
        })
      });
    };
    const file = acceptedFiles && acceptedFiles[0]
    uploadFile(file)

  }, [modal])
  const {getRootProps, getInputProps, isDragActive} = useDropzone({onDrop})


  return (
    <Box>

      {
        mode === 'delete' && <><Heading level={2}>Are you sure?</Heading>
        {
            modal && modal.photo && modal.photo.url && <Box height="small" width="small">
            <Image
              fit="cover"
              src={modal.photo.url}
            />
          </Box>
          }
        <Button label="delete" onClick={() => {
          onModal && onModal(mode, {
            ...modal,
            available: !!checked
          })
        }} />
        </>
      }
      {
        ['create', 'update'].includes(mode)  && <>
      <Heading level={3}>Upload Product</Heading>
      <TextInput 
        placeholder="Product Name"
        {...textInputProps('name', modal)}
      />
      <Box direction="row">
          <div {...getRootProps()}>
          <input {...getInputProps()} />
          {
            modal && modal.photo && modal.photo.url && <Box height="small" width="small">
            <Image
              fit="cover"
              src={modal.photo.url}
            />
          </Box>
          }
          {
            isDragActive ?
              <p>Drop the files here ...</p> :
              <p>Drag 'n' drop some files here, or click to select files</p>
          }
        </div>
      </Box>
      <TextInput {...textInputProps('startBid', modal)} placeholder="Start Bid" />
      <TextInput {...textInputProps('instantClosePrice', modal)} placeholder="Instant Close" />
      <CheckBox checked={checked} label="Display On Store" onChange={(event) => {
        setChecked(event.target.checked)
      }} /> 
      <Button label={mode} onClick={() => onModal && onModal(mode, {
          ...modal,
          available: !!checked
        })} />
      </>
      }
    </Box>
  );
};

export default Component;
